import React from 'react';
import { Row, Button, InfoMessage } from 'components';
import { reduxForm, Field } from 'redux-form';
import { validators } from 'utils';

const renderField = (props) => {
  const { label, type, meta, input } = props;
  const { error, touched } = meta;
  return (
    <Row>
      <div className="title">{label}:</div>
      <input {...input} type={type} />
      {touched && error &&
        <span className="form-error">{error}</span>
      }
    </Row>
  );
}

const mySubmit = values => {
  window.alert(JSON.stringify(values));
};

const FieldLevelForm = props => {
  const { handleSubmit, submitting, reset } = props;
  return (
    <div>
      <h3>Field Level Form</h3>
      <form onSubmit={handleSubmit(mySubmit)}>
        <Field
          name="account"
          label=""
          type="text"
          placeholder=""
          component={renderField}
          validate={[
            validators.required,
            validators.maxLength8
          ]}
        />
        <Field
          name="password"
          label=""
          type="password"
          placeholder=""
          component={renderField}
          validate={[
            validators.required,
            validators.number,
          ]}
        />
        <Field
          name="email"
          label=""
          type="text"
          placeholder=""
          component={renderField}
          validate={[
            validators.required,
            validators.email,
          ]}
        />
        <Button disabled={submitting} btnStyle="primary" type="submit"></Button>
        <Button type="button" onClick={reset}></Button>
      </form>
      <InfoMessage>
          <p>Field-level Validation  Sync Form,  validators </p>
          <p>Sync Form  Field-level  validators </p>
          <p> Field  validate  validators  String  undefined </p>
      </InfoMessage>
    </div>
  )
};

const formOptions = {
  form: 'fieldLevelForm',
};

export default reduxForm(formOptions)(FieldLevelForm);



