import React from 'react';
import { Row, Button, InfoMessage } from 'components';
import { reduxForm, Field } from 'redux-form';
import { connect } from 'react-redux';
import * as actions from 'actions';

const renderField = props => {
  const { type, label, meta, input } = props;
  const { touched, error } = meta;
  return (
    <Row>
      <div className="title">{label}: </div>
      <input type={type} {...input} />
      {touched && error &&
        <span className="form-error">{error}</span>
      }
    </Row>
  );
}

const SubmitForm = props => {
  const { handleSubmit, error, reset, submitting } = props;
  return (
    <div>
      <h3>Submit Validation</h3>
      <form onSubmit={handleSubmit(props.handleBackendAuth)}>
        <Field
          name="account"
          type="text"
          placeholder=""
          label=""
          component={renderField}
        />
        <Field
          name="password"
          type="password"
          placeholder=""
          label=""
          component={renderField}
        />
        <Button
          type="submit"
          btnStyle="primary"
          disabled={submitting}
        >
          {submitting ? '...' : ''}
        </Button>
        <Button type="button" onClick={reset}></Button>
        {error && <div style={{ color: 'red' }}>{error}</div>}
      </form>
      <InfoMessage>
        <p> jay/12345</p>
        <p>Submit Validation  API  3 </p>
        <p> API  action  handleSubmit Promise.  resolve,  Promise.reject  Redux-form  Error Object: SubmissionError</p>
        <p> SyncForm  validation  redux-form decorator</p>
        <p> Sync validation  Submit validation , </p>
      </InfoMessage>
    </div>
  );
}

const formOptions = {
  form: 'submitForm',
  // validate: validator,
};

const mapDispatchToProps = {
  handleBackendAuth: actions.handleBackendAuth,
};

export default reduxForm(formOptions)(
  connect(null, mapDispatchToProps)(SubmitForm)
);


