const maxLength = max => value => value && value.length > max ? ` ${max} ` : undefined;

/**
 * Export validators
 */

export const required = value => !value ? '': undefined;
export const maxLength8 = maxLength(8);
export const number = value => value && isNaN(Number(value)) ? '' : undefined;
export const email = value => value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value) ? ' email ' : undefined;


