import React from 'react';
import { connect } from 'react-redux';
import { Row, InfoMessage, Button } from 'components';
import * as actions from 'actions';
/**
 * Redux Form HOC
 */
import { Field, reduxForm } from 'redux-form';

const validater = values => {
  const errors = {};
  console.log('====validation function get values===========================');
  console.log(values);
  console.log('====================================');
  /** account validation  */
  if (!values.account) errors.account = "";
  /** password validation */
  if (!values.password) errors.password = "";
  return errors;
}

const warningChecker = values => {
  const { account, password } = values;
  const warnings = {};
  if (account && account.length > 6) warnings.account = "";
  if (password && password.length > 6) warnings.password = "";
  return warnings;
}

const renderField = (props) => {
  console.log('=====props into the renderField=================');
  console.log(props);
  console.log('== props: meta, label, type,  input==');

  const { input, type, label, meta } = props;
  const { touched, error, warning } = meta;

  return (
    <Row>
      <div className="title">{label}: </div>
      <input {...input} type={type} />
      {touched &&
        (
          (error && <span className="form-error">{error}</span>) ||
          (warning && <span className="form-warning">{warning}</span>)
        )
      }
    </Row>
  )
}

const mySubmit = dispatch => values => {
  const messages = `
    ====================================
    mySubmit  submit  handleSubmit  ( validation  )
    ${JSON.stringify(values)}
    ====================================
  `;
  alert(messages);
  dispatch(values);
}

const SyncForm = (props) => {
  const {
    handleSubmit, // redux-form handleSubmit(myFunc)
    handleFormSubmitByAction,
    reset,
   } = props;
  return (
    <div >
      <h3>Synchronous Form Validation</h3>
      <form onSubmit={handleSubmit(mySubmit(handleFormSubmitByAction))}>
        <Field name="account" label="" component={renderField} type="text" />
        <Field name="password" label="" component={renderField} type="password" />
        <Button type="submit" btnStyle="primary"></Button>
        <Button type="button" onClick={reset}></Button>
      </form>
      <InfoMessage>
        <p>Synchronous Form  input  dispatch actions: @@redux-form/EVENT</p>
        <p> validation/warning function  decorator  config parameter, props  decorated form component</p>
        <p> render  inline input, </p>
      </InfoMessage>
    </div>
  );
}

const mapDispatchToProps = {
  handleFormSubmitByAction: actions.handleFormSubmitByAction,
};

const formOptions = {
  form: 'syncForm', //  id,  formReducer  form
  validate: validater, //  function, 
  warn: warningChecker, //  function, 
};

export default reduxForm(formOptions)(connect(null, mapDispatchToProps)(SyncForm));


